.affair__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.filters {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  padding-left: 20px;
}

.name {
  margin: 10px;
  padding: 5px;
  border-width: 2px;
  border-style: solid;
  border-radius: 5px;
  border-color: #838996;
  width: 150px;
  background-color: rgba(222, 144, 218, 0.6);

}

.priority {
  padding: 5px;
  margin: 10px;
  background-color: rgba(220, 20, 60, 0.4);
  border-width: 2px;
  border-style: solid;
  border-radius: 5px;
  width: 90px;
  text-align: center;
  border-color: #838996;
  box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);

}

.affair {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.button__delete {
  border: none;
  outline: none;
  background: none !important;;
  color: red !important;
  padding: 0 !important;
}

.btn__filter {
  border: 2px solid rgb(92, 44, 89);
  width: 60px;
  background-color: rgba(138, 73, 134, 0.8) !important;
  color: white !important;
  margin: 5px !important;
  padding: 5px !important;
  border-radius: 5px;
}

.active {
  box-shadow: 4px 4px 8px 0px rgba(34, 60, 80, 0.2);
}